* {
  margin: 0;
  padding: 0;
}

:root {
  --p: rgb(255, 0, 111);
  --main: rgb(20, 241, 149);
}

body {
  background-color: black;
  font-family: sans-serif;
}

.app {
  background-color: black;
  height: 100%;
}

.home {
  background-color: black;
  background-color: black;
  /* height: 100%; */
}

.home-nav {
  color: white;
  display: flex;
  position: fixed;
  top: 0;
  width: 100%;
  background-color: black;
  border-bottom: 1px solid gray;
  margin-bottom: 5%;
}

.user {
  width: 25px;
  border-radius: 50px;
}

.home-nav ul {
  /* background-color: rgb(255, 0, 111); */
  color: white;
  display: flex;
  margin-left: auto;
  color: black;
}

.home-nav a {
  list-style-type: none;
  padding: 3vw;
  font-size: large;
  margin-top: 0px;
  font-weight: 600;
  font-family: sans-serif;
  margin-top: 0;
  color: white;
  cursor: pointer;
  text-decoration: none;
}

.home-nav a:hover {
  /* box-shadow: 0px 2px  rgb(255, 0, 111); */
  text-decoration: double;
  /* font-weight: 800; */
  color: var(--p);
}

.logo {
  float: left;
  margin-top: 5px;
  margin-left: 12px;
  color: var(--main);
  font-size: xx-large;
  padding: 12px;
  font-weight: bolder;

}

.title {
  margin-top: 10%;
  background-color: black;
  height: 70%;
  font-family: sans-serif;
  color: rgb(254, 253, 253);


}


.title h2 {
  display: flex;
  width: auto;
  margin-left: 10%;
  margin-top: 20vh;
  padding: 5px;
  font-size: xx-large;
  color: rgb(255, 255, 255);
}

.title-img {
  width: 50%;
  display: block;
  border-radius: 10px;
  margin-right: 10%;
  margin-top: -10%;
  margin-left: auto;
  height: auto;
}

.get-btn {
  background: linear-gradient(90deg, #d53369 0%, #daae51 100%);
  height: 50px;
  margin-left: 45%;
  margin-top: 0%;
  margin-bottom: 15%;
  color: black;
  font-weight: 600;
  font-size: large;
  border-radius: 50px;
  padding: 5px 15px;
  cursor: pointer;

}

.get-btn:hover {
  border-radius: 65px;
  /* box-shadow: 5px 5px gray ; */
  border: none;

}

.one {
  color: rgb(5, 5, 5);
  width: 85vw;
  margin-left: 0.5vw;
  margin-right: 0.5vw;
  margin-top: 10%;
  padding: 25px;
  border-radius: 5px;
  font-size: medium;
  font-family: sans-serif;
  background: linear-gradient(90deg, #00C9FF 0%, #92FE9D 100%);
}

.one h1 {
  font-family: sans-serif;
}

.two {
  color: black;
  width: 85vw;
  margin-left: 2%;
  margin-right: 3%;
  margin-top: 5%;
  padding: 25px;
  border-radius: 5px;
  font-size: medium;
  background: rgb(238, 174, 202);
  background: radial-gradient(circle, rgba(238, 174, 202, 1) 0%, rgba(148, 187, 233, 1) 100%);
  margin-bottom: 10%;

}

.two h1 {
  font-family: sans-serif;
}

.events {
  background: rgb(34, 193, 195);
  background: linear-gradient(0deg, rgba(34, 193, 195, 1) 0%, rgba(253, 187, 45, 1) 100%);
  margin-left: 3%;
  border-radius: 5px;
  padding: 15px;
}

.event-container {
  display: flex;
  overflow-x: scroll;
  padding: 10px;
  font-family: sans-se
}

.events h1 {
  font-family: sans-serif;
}

.event {
  padding: 15px;
  margin: 5px;
  height: 150px;
  background-color: black;
  color: white;
  border-radius: 0% 25% 0% 25%;
  margin-right: 5px;
  text-align: center;
  box-shadow: 2px 2px 2px orange, -2px -2px 2px orange;
}

.footer {
  margin-top: 5%;
  position: absolute;
  border-top: 1px ridge var(--p);
  width: 100%;
  /* box-shadow: 0px -5px 5px gray; */
  height: 120px;
  display: flex;
  flex-direction: row;
}

.footer h4 {
  margin: 15px;
  color: var(--main);
  margin: 15px;
  font-size: x-large;
}

.footer ul {
  color: white;
  margin: 15px;
}

.footer ul li {
  list-style-type: none;
  padding: 15px 20px;
  font-family: sans-serif;
  cursor: pointer;
}

.footer ul li:hover {
  margin-left: 5px !important;
  border-left: 2px solid var(--main);
  color: var(--main);
  font-weight: 800;

}

.copy {
  position: absolute;
  color: gray;
  bottom: -100px;
  padding: 10px;
  text-align: center;
  margin-top: 150px;
  margin-left: 25%;

}

.Login {
  font-family: sans-serif;
  background-color: black;
  color: white;
  margin-top: 20vh;
  /* display: flex;
  flex-direction: column; */
  height: auto;
  box-shadow: 0px 0px 10px gray;
  width: 350px;
  margin-left: 33%;
  border-radius: 25px;
  display: grid;
  gap: 2px;
  /* justify-content: center; */
}

.Login h2 {
  margin-top: 50px;
  color: white;
  text-align: center;
  padding: 25px;
  font-size: xxx-large;
  margin-bottom: 10%;
  font-weight: 100;

}

.Login input {
  margin-right: auto;
  margin-bottom: 5%;
  margin-left: 5%;
  background-color: rgb(2, 2, 2);
  display: grid;
  height: 45px;
  border-radius: 25px;
  /* text-align: center; */
  padding-left: 15px;
  border: 1px ridge white;
  width: 85%;
  font-family: sans-serif;
  color: white;
  font-size: medium;
}

*.swal2-popup {
  width: 480px;
  height: 280px;
  background-color: #313131;
  color: white !important;
  border-radius: 20px !important;
  border: 1px solid rgb(180, 179, 179) !important;
}

.swal2-container.swal2-backdrop-show,
.swal2-container.swal2-noanimation {
  background: rgba(0, 0, 0, .7) !important;
}

.Login span {
  color: red;
  font-family: sans-serif;
  margin-right: 25px;
  margin-left: 25px;
  margin-top: -15px;
  padding-bottom: 0px;
  font-weight: 500;
  text-align: center;
  font-size: small;
  width: 33%;
  padding: 0;
}

.Login span :last-child {
  width: 40%;
}

.Login button {
  margin-left: auto;
  margin-right: 5%;
  height: 50px;
  width: 50%;
  border-radius: 50px;
  cursor: pointer;
  background-color: white;
  font-weight: bold;
  font-size: large;
  color: black;
  font-weight: 900;
}

.login button:hover {
  background-color: var(--main);
}

.Login a:visited a:active {
  text-decoration: none;
  color: rgb(20, 241, 149);

}



.Login p {
  margin-top: 25px;
  margin-left: 20px;
  padding-bottom: 25px;
}

.Login a {
  margin-left: 15px;
  color: var(--main);
  text-decoration: none;
  font-size: medium;
}

.Login p a {
  text-decoration: none;
  color: var(--main);
  margin-left: 5px;

}

.Login .reg-btn {
  margin-left: 40%;
}

/* master class for dashboard component  */
.dashboard {
  font-family: sans-serif;
  color: white;
  display: flex;
}

.left {
  width: 8%;
  max-width: 100px;
  height: 97vh;
  border: 2px solid wheat;
  margin-left: 1%;
  margin-top: 1vh;
  border-radius: 20px;
}

.left a {
  color: white;
  text-decoration: none;
  display: block;
  text-align: center;
  margin-bottom: 5vh;
  font-size: large;
  font-weight: 900;
  padding: none;

}

.left a:hover {
  color: var(--p);
  background-color: rgb(81, 80, 80);
  width: auto;
  border-radius: 10px;

}


.left a:first-of-type {
  margin-top: 15vh;
}




/* logout button in dashboard */
.d-logout {
  margin-top: 40vh;
  color: gray;
  cursor: pointer;
  font-size: xx-large;
}

/* add user button in dashboard */
.d-adduser {
  font-size: xx-large;
  color: white;
}

.d-logout:hover {
  color: var(--p);
}

.d-adduser:hover {
  color: var(--main);
  cursor: pointer;

}

svg {
  width: 100%;
}


.right,
.t-right {
  margin: 1vh;
  width: 95%;
  margin-left: 10px;
  height: 97vh;
  border: 2px solid rgb(64, 25, 25);
  border-radius: 20px;
}

.upper-container {
  display: flex;
  margin-top: 2%;
}

/* for searc box in dashboard */
.upper-container input {
  margin-left: 2vw;
  height: 45px;
  background-color: black;
  border: 2px solid white;
  width: 40%;
  color: white;
  outline: none;
  border-radius: 50px;
  padding: 5px;
  text-align: center;
  font-weight: bold;
}

.upper-container .heading {
  margin-left: 2vw;
}

/* generate report */
.upper-container .generateR {
  margin-left: auto;
  margin-right: 1.5vw;
  border-radius: 10px;
  padding: 2px 5px;
  background-color: var(--main);
  color: black;
  font-weight: 600;
  cursor: pointer;
}

/* for user-search icon  */
.search-p {
  color: gray;
  font-size: xx-large;
  margin-left: 0vw;
  position: relative;
  left: 8vw;
  top: 1vh;
  width: auto;
}

/* container for overview and chart /second container after search and headings container  */
.d-container2 {
  display: flex;

}

.d-overview {
  border: 1px solid orange;
  /* background-color: rgb(1, 57, 37); */
  width: 50%;
  border: 3px solid;
  margin-left: 2%;
  margin-top: 3%;
  border-radius: 15px;
  height: 20vh;
  max-width: 50%;
  text-align: center;
  padding-bottom: 5px;
}

.d-balance {
  /* dashboard balance */
  color: white;
  font-size: xx-large;
  text-align: center;
  width: auto;
  height: 38%;
  padding: 7px;
}

.d-hl-container {
  /* contaner which contains higesh and lowest debitor in the list*/
  /* border :2px solid green; */
  margin-top: auto;
  margin-bottom: 0px;
  width: auto;
  height: 50%;
  display: flex;
  text-align: center;
}

.d-high {
  /* higest value*/
  width: 48%;
  margin-left: 5px;
  padding: 7px 5px;
  color: green;
  border: 1px solid gray;
  border-radius: 5px;
  text-align: center;
  padding: 10px;
  padding-top: 15px;
  overflow: hidden;


}

.d-high div span {
  /* background-color: red; */
  width: auto;
  height: auto;
  font-size: 1.1;
  padding: auto;

}

.d-high div {
  display: inline-flex;
  gap: 5px;
  color: white;
  text-transform: uppercase;
}

.d-low div {
  display: inline-flex;
  gap: 5px;
  color: white;
  text-transform: uppercase;
}

.d-low div span {
  font-size: 100%;
  padding: auto;
}

@media (max-width:1200px) {
  .d-low div span {
    font-size: small;
  }

  .d-high div span {
    font-size: small;
  }

}

@media (max-width:590px) {
  .d-low div span {
    font-size: x-small;
  }

  .d-low p,
  .d-high p {
    padding: 2px;
    font-size: xx-small;
  }

  .d-high div span {
    font-size: x-small;
  }

}


