  overflow: hidden;
}


.d-chart {
  width: 40%;
  margin-left: 2%;
  margin-top: 3%;
  border-radius: 15px;
  height: 20vh;
  border: 1px solid orange;
  /* box-shadow: 2px 2px 5px rgba(100,50,0,10); */
