}

canvas {
  text-align: center;
  margin-left: 25%;
}

.d-clients-container {
  border: 3px solid gray;
  height: 58.5vh;
  width: auto;
  margin-left: 2vw;
  margin-top: 2vh;
  margin-right: 2vw;
  border-radius: 18px;
  overflow-y: scroll;

}

.d-clients-container h5 {
  background-color: #00C9FF;
  border-radius: 15px 15px 0px 0px;
  text-align: center;
  padding: 5px;
  height: 20px;
  color: white;
  position: sticky;
  top: 0;
  font-size: x-large;

}

::-webkit-scrollbar {
  display: none;
}

.d-clients-container {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
