.add-t {
  position: absolute;
  padding: 10px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 320px;
  border-radius: 5px;
  background-color: #1c1b1b;
  border: none;
  border: 1px solid black;
  border-radius: 10px;
  box-shadow: 250px 110px 5000px 100px rgb(89, 86, 86), -110px -110px 5000px 100px rgb(89, 86, 86);
}

.add-t .buttons {
  display: flex;
  width: 100%;
}

.add-t .buttons button {
  width: 50%;
  border: none;
  cursor: pointer;
  height: 42px;
  font-weight: 750;
  /* border-radius: 10px; */
  color: white;
  font-size: large;
  background-color: #bbb;
  border: 1px ridge #050505;

}

.buttons {
  margin-bottom: 10px;
}

.in {
  border-radius: 0px 7px 0px 7px;
}

.out {
  border-radius: 0px 7px 0px 7px;
}

.add-t .buttons button :first-child {
  border-radius: 10px 0px 0px 10px;
}

.add-t h1 {
  text-align: center;
  padding: 0;
  margin: 0;
  /* margin-bottom: 5px; */
  background-color: #1c1b1b;
  border-radius: 10px 10px 0px 0px;
  width: 100;
  padding: 10px;
